import React, { FC } from 'react';
import PwaInstall from '../PWAInstall/PWAInstall.lazy';
import ScrollableComponent from '../ScrollableComponent/ScrollableComponent.lazy';
import './QuickNav.scss';

interface QuickNavProps {}

const QuickNav: FC = () => {  
  const getItems = (): JSX.Element[] => {
    return [{
      id: 1,
      label: "Learnings",
      tag:"#workedAndLearned-section"
    }, {
      id: 2,
      label: "My Work",
      tag:"#stackOverFlows-section"
    }, {
      id: 3,
      label: "Find me",
      tag:"#tools-section"
    }, {
      id: 4,
      label: "Articles",
      tag:"#myArticles-section"
    }].map((item: any) => {
      return (
        <a key={item.id} href={item.tag} className="quick-nav-item clear-button">
          <span className="quick-nav-item-label">{item.label}</span>
        </a>
      );
    })
  }

  return (
    <ScrollableComponent id="quick-nav">
      <PwaInstall/>  
      {getItems()} 
    </ScrollableComponent>
  );
}
export default QuickNav;
