import React, { lazy, Suspense } from 'react';
import { IScrollableComponentProps } from './ScrollableComponent';

const LazyScrollableComponent = lazy(() => import('./ScrollableComponent'));

const ScrollableComponent = (scrollComp:IScrollableComponentProps,props: JSX.IntrinsicAttributes & { children?: React.ReactNode; }) => (
  <Suspense fallback={null}>
    <LazyScrollableComponent children={scrollComp.children} id={scrollComp.id} {...props} />
  </Suspense>
);

export default ScrollableComponent;
