import React, { lazy, Suspense } from 'react';

const LazyPwaInstall = lazy(() => import('./PWAInstall'));

const PwaInstall = (props: JSX.IntrinsicAttributes & { children?: React.ReactNode; }) => (
  <Suspense fallback={null}>
    <LazyPwaInstall {...props} />
  </Suspense>
);

export default PwaInstall;
